import { supabase } from "@/lib/supabase";
import { redirect } from "next/navigation";
import { BadgeCheck, Plus, Package } from "lucide-react";
import Image from "next/image";

// This is a Server Component (Fast & Secure)
export default async function Dashboard() {
  
  // 1. Get the Current User
  const { data: { user } } = await supabase.auth.getUser();

  // If not logged in, kick them to login
  if (!user) {
    redirect("/login");
  }

  // 2. Check if they have a store
  const { data: store } = await supabase
    .from("stores")
    .select("*")
    .eq("owner_id", user.id)
    .single();

  // If NO store, kick them to Onboarding
  if (!store) {
    redirect("/onboarding");
  }

  // --- IF WE ARE HERE, THEY HAVE A STORE ---
  return (
    <div className="min-h-screen bg-gray-50">
      
      {/* Top Bar */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-10">
        <div className="max-w-5xl mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2">
             <div className="font-bold text-xl tracking-tight">StoreLink</div>
             <span className="bg-gray-100 text-xs px-2 py-1 rounded-md text-gray-500">Vendor</span>
          </div>
          <div className="flex items-center gap-3">
             <span className="text-sm font-medium text-gray-700 hidden sm:block">{store.name}</span>
             <div className="w-8 h-8 bg-gray-200 rounded-full overflow-hidden relative">
                {store.logo_url ? (
                  <Image src={store.logo_url} alt="Logo" fill className="object-cover" />
                ) : (
                  <div className="flex items-center justify-center h-full text-xs font-bold text-gray-500">{store.name[0]}</div>
                )}
             </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-5xl mx-auto px-4 py-8">
        
        {/* Welcome Section */}
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-8 gap-4">
          <div>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              Good afternoon, Vendor! 
              {store.subscription_plan === 'premium' && <BadgeCheck className="text-blue-500 w-6 h-6" />}
            </h1>
            <p className="text-gray-500 text-sm mt-1">Here is what's happening at <span className="font-medium text-gray-900">{store.name}</span> today.</p>
          </div>
          
          <div className="flex gap-3">
             <a 
               href={`/${store.slug}`} 
               target="_blank"
               className="px-4 py-2 bg-white border border-gray-200 text-gray-700 rounded-lg text-sm font-medium hover:bg-gray-50 transition"
             >
               View Shop
             </a>
             <button className="px-4 py-2 bg-gray-900 text-white rounded-lg text-sm font-medium shadow-md hover:bg-gray-800 transition flex items-center gap-2">
               <Plus size={16} /> Add Product
             </button>
          </div>
        </div>

        {/* Stats Grid (Placeholder) */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100">
              <p className="text-xs text-gray-400 font-medium uppercase">Total Products</p>
              <p className="text-2xl font-bold text-gray-900 mt-1">0</p>
           </div>
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100">
              <p className="text-xs text-gray-400 font-medium uppercase">Total Views</p>
              <p className="text-2xl font-bold text-gray-900 mt-1">0</p>
           </div>
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100">
              <p className="text-xs text-gray-400 font-medium uppercase">Plan</p>
              <p className="text-2xl font-bold text-green-600 mt-1 capitalize">{store.subscription_plan}</p>
           </div>
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100">
              <p className="text-xs text-gray-400 font-medium uppercase">Status</p>
              <p className="text-2xl font-bold text-gray-900 mt-1">Active</p>
           </div>
        </div>

        {/* Empty State */}
        <div className="bg-white rounded-3xl border border-dashed border-gray-200 p-12 text-center flex flex-col items-center">
           <div className="w-16 h-16 bg-gray-50 rounded-full flex items-center justify-center mb-4">
             <Package className="text-gray-300 w-8 h-8" />
           </div>
           <h3 className="text-lg font-bold text-gray-900">Your shelf is empty</h3>
           <p className="text-gray-500 text-sm max-w-xs mx-auto mt-2">
             You haven't added any products yet. Upload your first item to start selling.
           </p>
           <button className="mt-6 px-6 py-3 bg-gray-900 text-white rounded-xl text-sm font-bold shadow-lg hover:bg-gray-800 transition">
             Upload First Product
           </button>
        </div>

      </main>
    </div>
  );
}